import {customElement} from '@here/hds-base';
import {Checkbox} from '@here/hds-checkbox';
import {query} from 'lit/decorators.js';
import {styles} from './hds-toggle.css.js';

@customElement('hds-toggle')
export class Toggle extends Checkbox {
  @query('.-hds-checkbox-wrapper') checkboxWrapper!: HTMLElement;

  static get styles(): any {
    return [super.styles, styles];
  }

  protected firstUpdated() {
    this.checkboxWrapper!.classList.add('-hds-toggle-wrapper');
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-toggle': Toggle;
  }
}
